<template>
  <div>
    <!-- Â§¥ÈÉ®ÊêúÁ¥¢ -->
    <div class="searchWrap">
      <input class="searchInput" v-model="message" placeholder="üîéËØ∑ÂØπ‰æõÂ∫îÂïÜÂêçÁß∞ÊàñÁºñÂè∑Êàñ‰∫ßÂú∞ËøõË°åÊêúÁ¥¢" type="text">
      <div class="searchText putHover" @click="toSearchName(1)">ÊêúÁ¥¢ÂêçÁß∞</div>
      <div class="searchText putHover" @click="toSearchID(1)">ÊêúÁ¥¢ÁºñÂè∑</div>
      <div class="searchText putHover" @click="toSearchLocal(1)">ÊêúÁ¥¢‰∫ßÂú∞</div>
    </div>
    <!--titleÊòæÁ§∫‰ø°ÊÅØ  -->
    <div class="titleWrap">
      <div class="title">
        ‰æõÂ∫îÂïÜ‰ø°ÊÅØÂàóË°®Ôºö
      </div>
      <div class="titleRight">
        
        <div class="count">
          ÂÖ±Êúâ‰ø°ÊÅØ <span class="font_red font_blod font_mr">{{total}}</span>Êù°
        </div>
      </div>
      <div @click="showAddModel()" class="add">
        Êñ∞Â¢û‰ø°ÊÅØ<img src="@/assets/images/add.png" alt="">
      </div>
    </div>
    <!-- listÂÜÖÂÆπ -->
    <div class="listWrap ">
      <ul class="list shadow">
        <li class="listTitle listLi">
          <div class="li_div">‰æõÂ∫îÂïÜÁºñÂè∑</div>
          <div class="li_div">‰æõÂ∫îÂïÜÂêçÁß∞</div>
          <div class="li_div">‰æõÂ∫îÂïÜ‰∫ßÂú∞</div>
          <div class="li_div">ËØ¶ÁªÜÂú∞ÂùÄ</div>
          <div class="li_div">ËÅîÁ≥ª‰∫∫</div>
          <div class="li_div">ËÅîÁ≥ªÊñπÂºè</div>
          <div class="operate">Êìç‰Ωú</div>
        </li>
        <li  v-if='total<=0' class="nodata">
          ÊöÇÊó†Êï∞ÊçÆ
        </li>
        <li v-else class="listLi listContent" v-for="(item,i) in dataList" :key="i">
          <div class="li_div">{{item.supplierId}}</div>
          <div class="li_div">{{item.supplierName}}</div>
          <div class="li_div">{{item.originPlace}}</div>
          <div class="li_div">{{item.address}}</div>
          <div class="li_div">{{item.supplierUser}}</div>
          <div class="li_div">{{item.supplierPhone}}</div>
          <div class="operate">
            <span 
            @click="showEditModel(item.id,item.supplierId,item.supplierName,item.originPlace,item.address,item.supplierUser,item.supplierPhone)" 
            class="edit putHover font_mr">‰øÆÊîπ</span>
            <Poptip
              confirm
              title="Âà†Èô§ËØ•‰æõÂ∫îÂïÜÔºå‰ºöÂ∞Ü‰æõÂ∫îÂïÜÂêç‰∏ãÁöÑÊâÄÊúâÁâ©Êñô‰ø°ÊÅØÂèäËØ¢‰ª∑‰ø°ÊÅØÈÉΩ‰ºöË¢´Âà†Èô§ÔºåÁ°ÆÂÆöÂà†Èô§ÂêóÔºü"
              @on-ok="deleteSupplier(item.id)"
              @on-cancel="deletecancel"
               placement="bottom-end">
              <span  class="delete putHover font_mr">Âà†Èô§</span>
            </Poptip>
            <!-- <span @click="deleteSupplier(item.id)" class="delete putHover font_mr">Âà†Èô§</span> -->
          </div>
        </li>
      </ul>
      <!-- ÂàÜÈ°µÂå∫ -->
      <div class="pageWrap">
          <Page :total="total" :current="pageNo" :page-size='pageSize' @on-change = 'changePage' prev-text="‰∏ä‰∏ÄÈ°µ" next-text="‰∏ã‰∏ÄÈ°µ" />
      </div>
      <!-- modelÊΩ≠Ê°ÜÂå∫ -->
      <div>
        <Modal class="modelWrap" :closable="false" :mask-closable="false" v-model="addModel" width="360">
          <p class="modelTitle" slot="header">Ê∑ªÂä†‰æõÂ∫îÂïÜ</p>
          <div class="modelContent">
            <div class="type_content">
              <span class="input_name">‰æõÂ∫îÂïÜÂêçÁß∞Ôºö</span>
              <Input class="model_input" type="text" placeholder="ËØ∑ËæìÂÖ•‰æõÂ∫îÂïÜÂêçÁß∞" v-model="supplierName" />
            </div>
            <div class="type_content">
              <span class="input_name">‰æõÂ∫îÂïÜ‰∫ßÂú∞Ôºö</span>
              <Input class="model_input" type="text" placeholder="ËØ∑ËæìÂÖ•‰æõÂ∫îÂïÜ‰∫ßÂú∞" v-model="originPlace" />
            </div>
            <div class="type_content">
              <span class="input_name">ËØ¶ÁªÜÂú∞ÂùÄÔºö</span>
              <Input class="model_input" type="text" placeholder="ËØ∑ËæìÂÖ•ËØ¶ÁªÜÂú∞ÂùÄ" v-model="address" />
            </div>
            <div class="type_content">
              <span class="input_name">ËÅîÁ≥ª‰∫∫Ôºö</span>
              <Input class="model_input" type="text" placeholder="ËØ∑ËæìÂÖ•ËÅîÁ≥ª‰∫∫" v-model="supplierUser" />
            </div>
            <div class="type_content">
              <span class="input_name">ËÅîÁ≥ªÊñπÂºèÔºö</span>
              <Input class="model_input" type="tel" :maxlength=11 placeholder="ËØ∑ËæìÂÖ•ËÅîÁ≥ªÊñπÂºè" v-model="supplierPhone" />
            </div>
          </div>
          <div slot="footer">
            <div class="modelBtnWraP">
              <span class="cancel" @click="addCancel">ÂèñÊ∂à</span>
              <span class="confirm" @click="addConfirm">Á°ÆËÆ§</span>
            </div>
          </div>
        </Modal>

        <Modal class="modelWrap" :closable="false" :mask-closable="false" v-model="editModel" width="360">
          <p class="modelTitle" slot="header">
              ‰øÆÊîπ‰æõÂ∫îÂïÜ
          </p>
          <div class="modelContent">
            <div class="type_content">
              <span class="input_name">‰æõÂ∫îÂïÜÂêçÁß∞Ôºö</span>
              <Input class="model_input" type="text" placeholder="ËØ∑ËæìÂÖ•‰æõÂ∫îÂïÜÂêçÁß∞" v-model="supplierName" />
            </div>
            <div class="type_content">
              <span class="input_name">‰æõÂ∫îÂïÜ‰∫ßÂú∞Ôºö</span>
              <Input class="model_input" type="text" placeholder="ËØ∑ËæìÂÖ•‰æõÂ∫îÂïÜ‰∫ßÂú∞" v-model="originPlace" />
            </div>
            <div class="type_content">
              <span class="input_name">ËØ¶ÁªÜÂú∞ÂùÄÔºö</span>
              <Input class="model_input" type="text" placeholder="ËØ∑ËæìÂÖ•ËØ¶ÁªÜÂú∞ÂùÄ" v-model="address" />
            </div>
            <div class="type_content">
              <span class="input_name">ËÅîÁ≥ª‰∫∫Ôºö</span>
              <Input class="model_input" type="text" placeholder="ËØ∑ËæìÂÖ•ËÅîÁ≥ª‰∫∫" v-model="supplierUser" />
            </div>
            <div class="type_content">
              <span class="input_name">ËÅîÁ≥ªÊñπÂºèÔºö</span>
              <Input class="model_input" type="tel" :maxlength=11 placeholder="ËØ∑ËæìÂÖ•ËÅîÁ≥ªÊñπÂºè" v-model="supplierPhone" />
            </div>
          </div>
          <div slot="footer">
            <div class="modelBtnWraP">
              <span class="cancel" @click="editCancel">ÂèñÊ∂à</span>
              <span class="confirm" @click="editConfirm">Á°ÆËÆ§</span>
            </div>
          </div>
        </Modal>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'supplier',
  data () {
    return {
      message:'',
      dataList:[],
      total:0,
      pageNo:1,
      pageSize:1,
      addModel:false,
      supplierName:'',
      originPlace:'',
      address:'',
      supplierUser:'',
      supplierPhone:'',
      id:'',
      supplierId:'',
      editModel:false,
      searchType:0

    }
  },
  components:{
  },
  methods:{
    toSearchName(num){
      this.searchType= 1
      this.pageNo = num?num:1
      let $this = this
      this.$axios({
        method:'get',
        url:'supplier/page',
        params:{    //ËøôÈáåÊòØÂèëÈÄÅÁªôÂêéÂè∞ÁöÑÊï∞ÊçÆ
            supplierName:this.message,
            pageIndex:this.pageNo,
            limit:this.pageSize
        }
      }).then((response) =>{
        if(response.data.code =='200'){
          this.dataList = response.data.pagination.datas
          this.total = (response.data.pagination.total && response.data.pagination)?response.data.pagination.total:0
        }else{
          this.$Message.error(response.data.msg);
        }
      })
    },
    toSearchID(num){
      this.searchType= 2
      this.pageNo = num?num:1
      let $this = this
      this.$axios({
        method:'get',
        url:'supplier/page',
        params:{    //ËøôÈáåÊòØÂèëÈÄÅÁªôÂêéÂè∞ÁöÑÊï∞ÊçÆ
            supplierId:this.message,
            pageIndex:this.pageNo,
            limit:this.pageSize
        }
      }).then((response) =>{
        if(response.data.code =='200'){
          this.dataList = response.data.pagination.datas
          this.total = (response.data.pagination.total && response.data.pagination)?response.data.pagination.total:0
        }else{
          this.$Message.error(response.data.msg);
        }
      })
    },
    toSearchLocal(num){
      this.searchType= 3
      this.pageNo = num?num:1
      let $this = this
      this.$axios({
        method:'get',
        url:'supplier/page',
        params:{    //ËøôÈáåÊòØÂèëÈÄÅÁªôÂêéÂè∞ÁöÑÊï∞ÊçÆ
            originPlace:this.message,
            pageIndex:this.pageNo,
            limit:this.pageSize
        }
      }).then((response) =>{
        if(response.data.code =='200'){
          this.dataList = response.data.pagination.datas
          this.total = (response.data.pagination.total && response.data.pagination)?response.data.pagination.total:0
        }else{
          this.$Message.error(response.data.msg);
        }
      })
    },
    loadData(num){
      this.searchType= 0
      let $this = this
      this.pageNo = num?num:1
      this.$axios({
          method:'get', 
          url:'supplier/page',
          params:{    //ËøôÈáåÊòØÂèëÈÄÅÁªôÂêéÂè∞ÁöÑÊï∞ÊçÆ
              pageIndex:this.pageNo,
              limit:this.pageSize
          }
      }).then((response) =>{
        if(response.data.code =='200'){
          this.dataList = response.data.pagination.datas
          this.total = (response.data.pagination.total && response.data.pagination)?response.data.pagination.total:0
        }else{
          this.$Message.error(response.data.msg);
        }
      })
    },
    showAddModel(){
      this.addModel = true
      this.supplierName =''
      this.originPlace =''
      this.address =''
      this.supplierUser =''
      this.supplierPhone =''
       
    },
    addCancel(){
      this.addModel = false
      this.supplierName =''
      this.originPlace =''
      this.address =''
      this.supplierUser =''
      this.supplierPhone =''
    },
    addConfirm(){
      if(!this.supplierName){
        this.$Message.error(
          {
            content: '‰æõÂ∫îÂïÜÂêçÁß∞‰∏çËÉΩ‰∏∫Á©∫',
            duration: 2000
          }
        );
        return
      }
      if(!this.originPlace){
        this.$Message.error(
          {
            content: '‰æõÂ∫îÂïÜ‰∫ßÂú∞‰∏çËÉΩ‰∏∫Á©∫',
            duration: 2000
          }
        );
        return
      }
      if(!this.address){
        this.$Message.error(
          {
            content: 'ËØ¶ÁªÜÂú∞ÂùÄ‰∏çËÉΩ‰∏∫Á©∫',
            duration: 2000
          }
        );
        return
      }
      if(!this.supplierUser){
        this.$Message.error(
          {
            content: 'ËÅîÁ≥ª‰∫∫‰∏çËÉΩ‰∏∫Á©∫',
            duration: 2000
          }
        );
        return
      }
      if(!this.supplierPhone){
        this.$Message.error(
          {
            content: 'ËÅîÁ≥ªÁîµËØù‰∏çËÉΩ‰∏∫Á©∫',
            duration: 2000
          }
        );
        return
      }
      let $this = this
      this.$axios({
        method:'post',
        url:'supplier/save',
        data:$this.qs.stringify({    //ËøôÈáåÊòØÂèëÈÄÅÁªôÂêéÂè∞ÁöÑÊï∞ÊçÆ
            supplierName:this.supplierName,
            originPlace:this.originPlace,
            address:this.address,
            supplierUser:this.supplierUser,
            supplierPhone:this.supplierPhone,
        })
      }).then((response) =>{
        if(response.data.code =='200'){
          this.loadData()
          this.addModel = false
          this.supplierName =''
          this.originPlace =''
          this.address =''
          this.supplierUser =''
          this.supplierPhone =''
        }else{
          this.$Message.error(response.data.msg);
        }
      })
      
    },
    showEditModel(id,supplierId,supplierName,originPlace,address,supplierUser,supplierPhone){
      this.editModel = true
      this.id = id
      this.supplierId = supplierId
      this.supplierName = supplierName
      this.originPlace = originPlace
      this.address = address
      this.supplierUser = supplierUser
      this.supplierPhone = supplierPhone
    },
    editCancel(){
      this.editModel = false
      this.id = ''
      this.supplierId = ''
      this.supplierName =''
      this.originPlace =''
      this.address =''
      this.supplierUser =''
      this.supplierPhone =''
    },
    editConfirm(){
      if(!this.supplierName){
        this.$Message.error(
          {
            content: '‰æõÂ∫îÂïÜÂêçÁß∞‰∏çËÉΩ‰∏∫Á©∫',
            duration: 2000
          }
        );
        return
      }
      if(!this.originPlace){
        this.$Message.error(
          {
            content: '‰æõÂ∫îÂïÜ‰∫ßÂú∞‰∏çËÉΩ‰∏∫Á©∫',
            duration: 2000
          }
        );
        return
      }
      if(!this.address){
        this.$Message.error(
          {
            content: 'ËØ¶ÁªÜÂú∞ÂùÄ‰∏çËÉΩ‰∏∫Á©∫',
            duration: 2000
          }
        );
        return
      }
      if(!this.supplierUser){
        this.$Message.error(
          {
            content: 'ËÅîÁ≥ª‰∫∫‰∏çËÉΩ‰∏∫Á©∫',
            duration: 2000
          }
        );
        return
      }
      if(!this.supplierPhone){
        this.$Message.error(
          {
            content: 'ËÅîÁ≥ªÁîµËØù‰∏çËÉΩ‰∏∫Á©∫',
            duration: 2000
          }
        );
        return
      }
      let $this = this
      this.$axios({
        method:'post',
        url:'supplier/update',
        data:$this.qs.stringify({    //ËøôÈáåÊòØÂèëÈÄÅÁªôÂêéÂè∞ÁöÑÊï∞ÊçÆ
            id:this.id,
            supplierId:this.supplierId,
            supplierName:this.supplierName,
            originPlace:this.originPlace,
            address:this.address,
            supplierUser:this.supplierUser,
            supplierPhone:this.supplierPhone,
        })
      }).then((response) =>{
        if(response.data.code =='200'){
          this.loadData()
          this.editModel = false
          this.id = ''
          this.supplierId = ''
          this.supplierName =''
          this.originPlace =''
          this.address =''
          this.supplierUser =''
          this.supplierPhone =''
        }else{
          this.$Message.error(response.data.msg);
        }
      })
      
    },
    deleteSupplier(id){
      let $this = this
      this.$axios({
        method:'post',
        url:'supplier/deleteById',
        data:$this.qs.stringify({    //ËøôÈáåÊòØÂèëÈÄÅÁªôÂêéÂè∞ÁöÑÊï∞ÊçÆ
            id:id,
        })
      }).then((response) =>{
        if(response.data.code =='200'){
          this.$Message.success(response.data.msg);
          this.loadData()
        }else{
          this.$Message.error(response.data.msg);
        }
      })
    },
    deletecancel(){},
    changePage(e){
      switch(this.searchType) {
        case 0:
            this.loadData(e)
            break;
        case 1:
            this.toSearchName(e)
            break;
        case 3:
            this.toSearchID(e)
            break;
        case 4:
            this.toSearchLocal(e)
            break;   
        default:
            this.loadData(e)
    } 
    }

  },
  mounted(){
    this.loadData()
  }
  
}
</script>

<style scoped lang='less'>
.nodata{
  height: 200px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 16px;
}
.font_red{
  color: red;
}
.font_blod{
  font-weight: bold;
}
.font_mr{
  display: inline-block;
  margin:0 3px ;
}
.searchWrap{
  padding: 15px 60px;
  display: flex;
  justify-content: center;
  align-items: center;
  .searchInput{
    height: 40px;
    flex: 1;
    border:gainsboro 1px solid;
    border-radius:  4px;
    padding: 15px 5px;
  }
  .searchText{
      margin-left: 40px;
    width: 80px;
    height: 40px;
    line-height: 40px;
    border:#26a2ff 1px solid;
    border-radius: 4px;
    font-size: 14px;
    color: white;
    background: #26a2ff;
  }
}
.titleWrap{
  display: flex;
  align-items: center;
  height: 60px;
  padding: 0 30px;
  .title{
    text-align: left;
    width: 200px;
    font-size: 16px;
    font-weight: bold;
  }
  .titleRight{
    flex: 1;
    font-size: 14px;
    .count{
      flex: 1;
      display: flex;
      justify-content: flex-end;
      align-items: center;
    }
  }
  .add{
    width: 100px;  
    display: flex;
    justify-content: flex-end;
    align-items: center;
    color: orange;
    img{
        margin: 0 5px;
        width: 16px;
        height: 16px;
    }
 }
  
}
.listWrap{
  margin: 30px;
  .list{
    .listLi{
      padding: 5px;
      height:50px ;
      display: flex;
      align-items: center;
      font-size: 16px;
      border-bottom: #909090 1px solid;
      &:last-child{
        border-bottom: none;
      }
      .li_div{
        flex: 1;
        padding: 0 3px;
      }
      .leftText{
        width: 200px; 
        text-align: left;
        padding: 0 3px;
      }
      .operate{
        width: 150px;
        padding: 0 3px;
        .edit{
          font-size: 14px;
          color: #26a2ff;
        }
        .delete{
          font-size: 14px;
          color: red; 
        }
      }

    }
    .listTitle{
      background: #a99cdd;
      color: white;
      border-radius: 4px 4px 0 0;
    }
    .listContent{
      font-size: 14px;
    }
    
  }
  .pageWrap{
      padding: 30px;
      height: 40px;
  }
}
.modelWrap{
  .modelTitle{
      text-align: center;
    }
    .modelContent{
      .type_content{
        display: flex;
        align-items: center;
        justify-content: flex-start;
        margin-bottom: 20px;
        .input_name{
          width: 85px;
          padding: 0 5px;
          height: 40px;
          display: flex;
          align-items: center;
          justify-content: flex-start;
          margin: 0 10px;
        }
        .model_input{
          flex: 1;
        }
        

      }
    
    }
    .modelBtnWraP{
      display: flex;
      align-items: center;
      justify-content: center;
      height: 60px;
      span{
        display: flex;
        align-items: center;
        justify-content: center;
        width: 90px;
        margin: 0 30px;
        height: 30px;
        border-radius: 4px;
        border: #26a2ff 1px solid;
        cursor: pointer;
        &.confirm{
          background: #26a2ff;
          color: white;
        }
      }
    }
}
.ivu-poptip-confirm .ivu-poptip-body .ivu-icon{
  position: relative;
}
</style>



